

.content-admin{
    margin-top: 5rem;
   display: flex;
   flex-direction: column;
   
}

.content-admin .navegacion{
    height: 15%;
    width: 90%;
    margin: 0 auto;
    font-size: 1.8rem;
    display: grid;
    grid-template-columns: 16% 16% 16% 16% 16%;
    gap: 5%;
}
.content-admin div{
    height: 85%;
    width: 90%;
    margin: 10px auto;
}

.content-admin .navegacion a{
    color: black;
    text-align: center;
    font-size: 2rem;
    text-decoration: none;
    padding: 10px 10px ;
   
    cursor: pointer;
}

.content-admin .navegacion a:hover{
    border: 1px solid;
    background-color: #F5F5F5;
    
}

.form-admin-2 ul {
    list-style-type: none;
    display: grid;
    grid-template-columns: 45% 45%;
    align-items: center;
    column-gap: 10%;
}

.form-admin-2 ul li{
    display: grid;
    grid-template-columns: 45% 45%;
    column-gap: 10%;
}
.form-admin-2 ul li input{
    height: 80%;
    width: 80%;
    margin: auto;
}

.sumit-admin-2 {
    grid-column-start: 2;
    display: flex;
    justify-content: center;
}
.sumit-admin-2 button{
    width: 8rem;
    cursor: pointer;
}

.basicTabla{
    table-layout: fixed;
  width: 100%;
  border-collapse: collapse;
  border: 3px solid;
  text-align: center;
  padding-bottom: 30px;
}

.basicTabla thead{
    background-color: white;
}
.basicTabla thead th:nth-child(1) {
    width: 8%;
  }
  
.basicTabla  thead th:nth-child(2) {
    width: 25%;
  }
  
.basicTabla thead th:nth-child(3) {
    width: 30%;
  }
  
.basicTabla thead th:nth-child(4) {
    width: 15%;
  }
  .basicTabla thead th:nth-child(5) {
    width: 15%;
  }
  .basicTabla thead th:nth-child(6) {
    width: 15%;
  }
  .basicTabla thead th:nth-child(7) {
    width: 15%;
  }
  .basicTabla thead th:nth-child(8) {
    width: 15%;
  }
.basicTabla th, td {
    padding: 15px;
  }

  .basicTabla tbody tr:nth-child(odd) {
    background-color: white;
  }
  
  .basicTabla tbody tr:nth-child(even) {
    background-color: white;
  }


