/*ventana modal */

  #modal12{
    background-color: rgba(0, 0, 0, 0.301);
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
  margin: 0;
  display: flex;
  justify-content: center;
    align-items: center;
    
}
.ocultar-modal{
    display: none;
}
.modal-content{
    position: absolute;
    background-color: white;
    width: 95%;
    
    padding: 20px;
    height: 80%;
}

.modal-btn-colsed{
    position: absolute;
    right: 3px;
    top: 3px;
    border: none;
    background: none;
    outline: none;
    font-size: 20px;
    cursor: pointer;
}

#dataTable{
    display: grid;
    grid-template-columns: 45% 45%;
    column-gap: 10%;
    justify-content: center;
    align-items: center;
}

#dataTable div{
    display: grid;
    grid-template-columns: 45% 45%;
    column-gap: 10%;
}

#botonesModal{
    position: absolute;
    right: 3px;
    bottom: 3px;
    border: none;
    background: none;
    outline: none;
    font-size: 20px;
    
    height: 7rem;
    width: 30rem;
}

#botonesModal button{
    height: 5rem;
    width: 12rem;
    cursor: pointer;
}


