#dataTable2 div{
  display: grid;
  grid-template-columns: 24% 24% 24% 24%;
  column-gap: 1%;
  justify-content: center;
  align-items: center;
}

#dataTable2 div div{
  display: grid;
  grid-template-columns: 45% 45%;
  column-gap: 10%;
}

#dataTable2 div#botonesModal2{
  position: absolute;
