html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

*{
  font-family: "BeaufortforLOL-Bold";
}

html {
  font-size: 62.5%;
}

body {
  font-family: 'Lato', sans-serif;
  font-size: 1.6rem;
  line-height: 2;
}

img {
  width: 100%;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.f-end {
  display: flex;
  justify-content: flex-end;
}
.texto-mayuscula{
  text-transform: uppercase;
}
.f-center {
  display: flex;
  justify-content: center;
}

.f-space-around {
  display: flex;
  justify-content: space-around;
}

.f-space-between {
  display: flex;
  justify-content: space-between;
  
}

.fondo-morado {
  background-color: #D9CEEF;
}

.centrar-texto {
  text-align: center;
  font-size: 2.25rem;
}

.boton {
  color: white;
  font-weight: 500;
  text-decoration: none;
  font-size: 2.5rem;
  padding: .5rem 2rem;
  margin-top: 2rem;
  margin-bottom: 2rem;
  display: inline-block;
  text-align: center;
  border-radius: .5rem;
  cursor: pointer;
}
.content-form{
  border: 1px solid #B5B5B5;
  background-color: #F5F5F5;
  width: 70rem;
  margin: 0 auto;
  font-size: 2rem;
  height: 270px;
 padding: 70px 20px;

}

.content{
  height: 600px;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.formulario{
  display: grid;
  grid-template-columns: 100%;
  gap: 10px;
}

.formulario div{
  display: grid;
  grid-template-columns: 50% 50%;
  
}
.formulario div.f-end {
  display: flex;
  justify-content: flex-end;
  
}